<template>
  <div class="recommend">
    <sticky :choice="1"></sticky>
    <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
      <crunchies :content="list"></crunchies>
    </van-list>
  </div>
</template>

<script>
import { List } from "vant";
import axios from "axios";
import crunchies from "../component/crunchies"; //榜单应用列表
import sticky from "../component/sticky";
export default {
  components: {
    crunchies,
    sticky,
    [List.name]: List
  },
  computed: {},
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      active: 0,
      page: 1,
      total:null
    };
  },
  mounted() {},
  methods: {
    onLoad() {
      // 异步更新数据
      this.loading = true;
      axios({
        method: "post",
        url: process.env.VUE_APP_API + "getCard.do",
        data: {
          requestid: "mmap_game_rank_v1",
          currentPage: this.page++,
          totalRows: this.total,
        },
        headers: {
          "Content-Type": "application/json",
          appname: "MM7.3.2.001.01_CTAndroid_JT",
          ua: "android-24-1080x2040-RNE-AL00"
        }
      })
        .then(response => {
          var packageAwardList = response.data.list;
          this.total = response.data.pageInfo.totalRows;
          console.log("resList", packageAwardList);
          if (this.list.length == 0) {
            this.list = response.data.list;
          } else {
            response.data.list.forEach((item, index, array) => {
              this.list.push(item);
            });
          }
          console.log("List", this.list);
          // }
          // 加载状态结束
          this.loading = false;
          // 数据全部加载完成
          if (this.list.length >= response.data.pageInfo.totalRows) {
            this.finished = true;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  }
};
</script>

<style lang="less">
.grid {
  .iconImg {
    width: 54px;
    height: 54px;
    margin: 0 10px;
  }
  .iconName {
    margin-top: 10px;
    font-size: 12.5px;
  }
}
.separate {
  height: 6px;
  background: rgba(242, 242, 242, 1);
}
.van-cell--large .van-cell__title {
  font-size: 18px;
  margin-bottom: 5px;
}
</style>
